
<template>
  <div style="padding-top:120px;">
    <div class="outer_label">
      <img
        class="inner_label login_logo"
        src="../../assets/img/icon/logo@3x.png"
      />
    </div>
    <div class="login_form">
      <input
        type="text"
        class="qxs-ic_user qxs-icon"
        placeholder="用户名"
        v-model="userName"
      />
      <span class="mang"><van-icon name="manager" color="white" /></span>
      <input
        type="text"
        class="qxs-ic_password qxs-icon"
        placeholder="密码"
        v-model="password"
      />
      <span class="mang1"><van-icon name="bell" color="white" /></span>
    </div>

    <div style="text-align:center">
    <van-button plain hairline color="#253238" @click="_login" round >登录</van-button >
    </div>
  </div>
</template>
<script>
import { login, getPersonalInfo } from '@/api';
export default {
  name: "",
  data() {
    return {
      userName: "",
      password: "",
      isBtnLoading: false,
    };
  },
  created() {
    this.checkLogin();
  },
  computed: {
    btnText() {
      if (this.isBtnLoading) return "登录中...";
      return "登录";
    },
  },
  mounted(){
    document.title = '登录';
  },
  methods: {

    /**
     * 检查登录状态
     * 如果登录状态有效，则返回上一个页面
     */
    checkLogin(){
      // if(sessionStorage.getItem('token')) this.$router.push('/');
      let token = sessionStorage.getItem('token') || localStorage.getItem('token');
      if(token){
        sessionStorage.setItem('token',token);

        getPersonalInfo().then(res=>{
          if (res.code == 0) {
            sessionStorage.setItem("token", res.data.token);
            sessionStorage.setItem("userId", res.data.userId);
            sessionStorage.setItem("userShop", res.data.userShop);
            sessionStorage.setItem("shopCode", res.data.shopCode);
            sessionStorage.setItem(
              "headimgurl",
              this.$utils.dealImg(res.data.userHeader)
            );
            sessionStorage.setItem("userName", res.data.userName);
            sessionStorage.setItem("userName2", res.data.userName2 || "");
            sessionStorage.setItem("userMobile2", res.data.userMobile2 || "");
            this.$router.push(sessionStorage.getItem('url'));
          }
        })
        
      }
    },
    _login() {
      if (!this.userName) {
        this.$toast.fail("请输入用户名");
        return;
      }
      if (!this.password) {
        this.$toast.fail("请输入密码");
        return;
      }

      login({
        account_number: this.userName,
        code: this.password,
        isCookie: true,
        openid: ''
      }).then(res=>{
        if(res.code === 0){

          localStorage.setItem('token',res.data.token);
          sessionStorage.setItem("token", res.data.token);
          sessionStorage.setItem("userId", res.data.userId);
          sessionStorage.setItem("userShop", res.data.userShop);
          sessionStorage.setItem("shopCode", res.data.shopCode);
          sessionStorage.setItem(
            "headimgurl",
            this.$utils.dealImg(res.data.userHeader)
          );
          sessionStorage.setItem("userName", res.data.userName);
          sessionStorage.setItem("userName2", res.data.userName2 || "");
          sessionStorage.setItem("userMobile2", res.data.userMobile2 || "");

          this.$router.push('/');
          this.$toast.success(res.msg);
        }else{
          this.$toast.fail(res.msg);
        }
      })
    }
  },
};
</script>
<style lang="scss" scoped>
