  text-align: center;
}
.login_form {
  position: relative;
  margin-top: 15px;
}
.qxs-ic_user {
  margin-top: 15px;
  margin-bottom: 5px;
  margin-left: 45px;
  width: 70%;
  height: 24px;
}
.qxs-ic_password {
  margin-top: 15px;
  margin-bottom: 5px;
  margin-left: 45px;
  width: 70%;
  height: 24px;
}
.phonenumber {
  list-style-type: none;
  margin-top: 15px;
  margin-bottom: 5px;
  margin-left: 45px;
}
.flexbox {
  display: flex;
}
.login_form {
  text-align: left;
}
.inputText {
}
.van-button--normal {
  padding: 0 100px;
  margin-top: 20px;
}
.act1 {
  display: flex;
  justify-content: center;
  margin-top: 15px;
  height: 20px;
  line-height: 20px;
  color: white;
}
.line {
